<template>
    <div class="page-content" :style="mianStyle">
        <div class="cs-btn-backMap" @click="backToMap"><i class="el-icon-arrow-left"></i></div>
        <div class="el-row contBox clear">
            <div :class="[onlyOneClass, isShow_bjxq ? twoClass : '']">
                <div class="cs-filter-box">
                    <!-- 筛选 -->
                    <div class="cs-export-box" v-if="false"><el-button :plain="true" type="primary" >导出</el-button></div>
                    <el-button-group>
                        <el-button :plain="flag != 0" type="primary" @click="filterTag(0)">未处理</el-button>
                        <el-button :plain="flag != 1" type="primary" @click="filterTag(1)">已处理</el-button>
                    </el-button-group>
                    <div class="cs-searchBox ">
                        <input type="text" v-model="keyword" placeholder="报警人" autocomplete="off" class="el-input__inner cs-search_input" @keyup.enter="jq_search">
                        <img src="../../img/lookup.png" @click="jq_search" alt="搜索">
                    </div>

                </div>
                <div class="cs-list-table">
                    <!-- 列表 -->
                    <el-table :data="showlist" border style="width: 100%" @row-click="handleRowHandle" @selection-change="handleSelectionChange">
                        <el-table-column
                                type="index"
                                label="序号"
                                width="65">
                        </el-table-column>

                        <el-table-column
                                prop="alertUserRealname"
                                label="报警人姓名" width="110">
                        </el-table-column>

                        <el-table-column
                                prop="cellphone"
                                label="联系电话">
                        </el-table-column>

                        <el-table-column
                                prop="alertTime"
                                label="报警时间">
                            <template scope="scope">{{ scope.row.alertTime }}</template>
                        </el-table-column>


                        <el-table-column
                                prop="alertTime"
                                label="最后更新时间">
                            <template scope="scope">{{ scope.row.updateTime }}</template>
                        </el-table-column>


                        <el-table-column
                                prop="type"
                                label="警情来源">
                            <template scope="scope2">
                                <span v-if="scope2.row.type ===1">一键报警</span>
                                <span v-if="scope2.row.type ===2">随手记</span>
                                <span v-if="scope2.row.type ===3">随手拍</span>
                            </template>
                        </el-table-column>
                        <el-table-column
                                prop="status"
                                label="状态">
                            <template scope="scope3">
                                <span v-if="scope3.row.status ===0">未处理</span>
                                <span v-if="scope3.row.status ===1">已处理</span>
                            </template>
                        </el-table-column>


                        <el-table-column
                                label="操作">
                            <template scope="scope">

                                <el-button v-if="scope.row.status ===0" v-on:click.native.prevent="handleRow(scope.$index, scope.row)" type="text" size="small">处理</el-button>
                                <el-button v-if="scope.row.status !=0" v-on:click.native.prevent="handleRow(scope.$index, scope.row)" type="text" size="small">查看</el-button>
                                <!-- <el-button type="text" size="small">视频通话</el-button> -->

                            </template>
                        </el-table-column>


                    </el-table>
                    <!-- 分页 -->
                    <div class="cs-footer-page">
                        <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage4"
                            :page-sizes="[10, 20 ,30 ,50]"
                            :page-size="page_size"
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="records">
                        </el-pagination>
                    </div>
                </div>

            </div>
            <div id="openinfo"  class="el-col el-col-24 el-col-xs-24 el-col-md-12 cs-col-right-box" v-if="isShow_bjxq" >
                <bjxqcomponent ref="bjxqcomponent" :jqfb_list_detail="jqfb_list_detail" :mapHeight="mapHeight"  @hidden_bjxq="hidden_bjxq"></bjxqcomponent><!--报警详情 -->
            </div>
        </div>
    </div>
</template>

<script>


export default {
    props: {
        mapHeight:{
            type:Number,
            default:600
        }
    },
    data() {
        return {
            page_size:10,
            twoClass: 'el-col-md-12',
            onlyOneClass: 'el-col el-col-24 el-col-xs-24',
            isShow_bjxq:false,
            mianStyle:{},
            flag:0,
            records:0,
            showlist:[],
            multipleSelection: [],
            currentPage4: 1,
            keyword : "",
            jqfb_list_detail:{}
        }
    },
    components: {

        bjxqcomponent: function (resolve, reject) {
            require(['./bjxq2.vue'],  resolve)
        }


    },
    methods: {
        backToMap(){
            this.$emit('hideList');
        },
        handleRow(index, rows) {
            this.isShow_bjxq = true;
            const Bus = global.eventHub;
            this.jqfb_list_detail=rows;

        },
        hidden_bjxq() {
            const Bus = global.eventHub;
            this.isShow_bjxq = false;

        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        handleSizeChange(val) {
            this.page_size=val;
            var that=this;
            that.getListData();
        },
        handleCurrentChange(val) {
            var that=this;
            this.currentPage4 = val;
            that.getListData();
        },
        handleRowHandle(row){
            this.handleRow("",row);
        },
        filterTag(type) {
            let that = this;
            that.flag = type;
            that.getListData();
            this.isShow_bjxq=false;
        },
        jq_search: function() {
            //this.showlist=this.tableData3;
            var that = this;
            var res = [];
            that.getListData();
        },
        getListData : function () {
            let that = this;
            var _param = {
                token:global.data.getToken(),
                page:that.currentPage4,
                pageSize:that.page_size,
                keyword: that.keyword
            };
            _param.status = that.flag;
            that.$http.post(global.config.weburl+'api/alert/queryAlert.do',_param).then(function(res){
                if(res.data.code==0){
                    var _data= res.data;
                    that.showlist = _data.data;
                    that.records = _data.pageInfo.records;
                } else{
                    global.tools.handleError(res.data);
                }

            },function(error){
                this.$message({
                    type: 'error',
                    message: "获取数据失败"
                });
            });
        },
        addListener:function() {
            const that = this;
            const Bus = global.eventHub;

            Bus.$on('doto_jqlb_refresh', function () {
                //显示报警详情
                console.log("显示报警详情00000000000000000000000000000000000000");
                that.getListData();
            });
        },
        removeListener:function() {
            const that = this;
            const Bus = global.eventHub;
            console.log("removeListener");
            console.log("----removeListener");
            Bus.$off('doto_jqlb_refresh', function () {
                //显示报警详情
                that.getListData();
            });
        }
    },
    created: function(){
        var that=this;
        this.mianStyle = {
            height:this.mapHeight +"px"
        }
        that.getListData();

    },
    mounted:function() {
        const that = this;
        that.addListener();
    },
    beforeDestroy: function () {
        this.removeListener();
    }
}
</script>



